import React from "react";
import Avatar from "./Avatar";
import { buildAvatarString, AvatarConfig, parseAvatarString } from "../lib/avatar";
import {
  faceCtx,
  hair0Ctx,
  hair1Ctx,
  clothing0Ctx,
  clothing1Ctx,
  faceTextureCtx,
  centerClothingCtx,
  rightClothingCtx,
  leftClothingCtx,
  glassesCtx,
} from "../lib/assetContexts";
import "./AvatarBuilder.css";
import ColumnPager from "./common/ColumnPager";
import Tabs from "./common/Tabs";
import Swatch from "./common/Swatch";
import Tile from "./common/Tile";
import { SKIN_COLORS, CLOTH_COLORS, HAIR_COLORS, BG_COLORS, toHex, ENABLE_BG_ANTI_COLLISION } from "../styles/palette";
import ofekLogo from "../Assets/logo/AvatarCreator.svg";
import Button from "./common/Button";

type Tab = "Face" | "Hair" | "Clothing" | "Details" | "Background";

// Palettes are imported from styles/palette

function extractNumbers(keys: string[], re: RegExp, group = 1): number[] {
  const set = new Set<number>();
  for (const k of keys) {
    const m = k.match(re);
    if (m) {
      set.add(Number(m[group]));
    }
  }
  return Array.from(set).sort((a, b) => a - b);
}

const faceOptions = extractNumbers(faceCtx.keys(), /Face_(\d+)\.svg/);
const hairOptions = extractNumbers(hair0Ctx.keys().concat(hair1Ctx.keys()), /Hair_(\d{3})_\d\.svg/);
const clothingOptions = extractNumbers(clothing0Ctx.keys().concat(clothing1Ctx.keys()), /Clothing_(\d+)_\d\.svg/);
const faceTextureOptions = extractNumbers(faceTextureCtx.keys(), /FaceTexture_(\d+)\.svg/);
const centerClothingOptions = extractNumbers(centerClothingCtx.keys(), /CenterClothing_(\d+)\.svg/);
const rightClothingOptions = extractNumbers(rightClothingCtx.keys(), /RightClothing_(\d+)\.svg/);
const leftClothingOptions = extractNumbers(leftClothingCtx.keys(), /LeftClothing_(\d+)\.svg/);
const glassesOptions = extractNumbers(glassesCtx.keys(), /Glasses_(\d+)\.svg/);

export default function AvatarBuilder() {
  const [tab, setTab] = React.useState<Tab>("Face");

  const [cfg, setCfg] = React.useState<AvatarConfig>({
    bgColor: BG_COLORS[0],
    bodyColor: CLOTH_COLORS[1],
    skinColor: SKIN_COLORS[2],
    hairColor: HAIR_COLORS[0],
    faceType: faceOptions[0] ?? 1,
    hairType: hairOptions[0] ?? 1,
    clothingType: clothingOptions[1] ?? 1,
    glasses: 0,
  });

  // Change log state and helpers
  const [logs, setLogs] = React.useState<string[]>([]);
  const log = React.useCallback((msg: string) => {
    const stamped = `${new Date().toLocaleTimeString()} — ${msg}`;
    // eslint-disable-next-line no-console
    console.log(stamped);
    setLogs((xs) => [stamped, ...xs].slice(0, 200));
  }, []);

  type Key = keyof AvatarConfig;
  const keys: Key[] = [
    "bgColor",
    "bodyColor",
    "skinColor",
    "hairColor",
    "faceType",
    "hairType",
    "clothingType",
    "faceTexture",
    "centerClothing",
    "rightClothing",
    "leftClothing",
    "eyes",
    "nose",
    "mouth",
    "glasses",
  ];

  const applyUpdate = React.useCallback(
    (updater: (prev: AvatarConfig) => AvatarConfig, reason?: string) => {
      setCfg((prev) => {
        const next = updater(prev);
        const changed: string[] = [];
        for (const k of keys) {
          const a = prev[k];
          const b = next[k];
          if (a !== b) changed.push(`${String(k)}: ${a ?? ""} -> ${b ?? ""}`);
        }
        if (changed.length) {
          log(`${reason ? reason + " — " : ""}${changed.join(", ")}`);
        }
        return next;
      });
    },
    [log]
  );

  const onSave = () => {
    const s = buildAvatarString(cfg);
    // Save/copy only the raw string
    const snippet = s;
    // Expose on window for quick access during the session
    try {
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      (window as any).AVATAR_STRING = s;
    } catch {}
    // Persist a copy (optional), and copy to clipboard
    try {
      localStorage.setItem("AVATAR_STRING", s);
    } catch {}
    if (navigator?.clipboard?.writeText) {
      navigator.clipboard.writeText(snippet).then(
        () => {
          // eslint-disable-next-line no-console
          console.log("Saved & copied to clipboard:", snippet);
          alert("Saved and copied to clipboard!\n" + snippet);
        },
        () => {
          // eslint-disable-next-line no-console
          console.log("Saved (clipboard unavailable):", snippet);
          alert("Saved! (Could not copy automatically)\n" + snippet);
        }
      );
    } else {
      // eslint-disable-next-line no-console
      console.log("Saved (no clipboard API):", snippet);
      alert("Saved! (Clipboard API unavailable)\n" + snippet);
    }
  };

  // Helpers to set/toggle numeric options
  const setNumeric = <K extends keyof AvatarConfig>(key: K, value: number) =>
    applyUpdate((x) => ({ ...x, [key]: value } as AvatarConfig), `Set ${String(key)}`);
  const toggleNumeric = <K extends keyof AvatarConfig>(key: K, value: number) =>
    applyUpdate(
      (x) => ({ ...x, [key]: (x[key] as unknown as number) === value ? 0 : value } as AvatarConfig),
      `Toggle ${String(key)} ${value}`
    );

  const onImportPreset = () => {
    const input = window.prompt("Paste avatar string (CSV) or JSON AvatarConfig:");
    if (!input) return;
    try {
      let patch: AvatarConfig | null = null;
      if (input.trim().startsWith("{")) {
        patch = JSON.parse(input) as AvatarConfig;
      } else {
        patch = parseAvatarString(input);
      }
      if (patch) {
        applyUpdate((prev) => ({ ...prev, ...patch }), "Import preset");
        alert("Imported preset successfully");
      }
    } catch (e) {
      alert("Failed to import preset: " + (e as Error).message);
    }
  };

  return (
    <div className="builder-root">
      <div
        className="builder-hero"
        style={{ ["--hero-bg" as unknown as string]: toHex(cfg.bgColor) || "#E72787" } as React.CSSProperties}
      >
        <div className="builder-title">
          <img className="avatarCreator-logo" src={ofekLogo} alt="Ofek Logo" />
        </div>
        <div className="builder-preview">
          <Avatar config={cfg} hideBackground cover={true} width="100%" height="100%" />
        </div>
      </div>

      <div className="builder-body">
        <Tabs
          items={[
            { key: "Face", label: "Face" },
            { key: "Hair", label: "Hair" },
            { key: "Clothing", label: "Clothing" },
            { key: "Details", label: "Details" },
            { key: "Background", label: "Background" },
          ]}
          activeKey={tab}
          onChange={(k) => setTab(k as Tab)}
        />
        <div className="container-tilesnswatches">
          {tab === "Face" && (
            <div>
              <div className="swatch-row">
                {SKIN_COLORS.slice(0, 7).map((c) => (
                  <Swatch
                    key={c}
                    color={toHex(c) as string}
                    selected={(toHex(cfg.skinColor) || "").toLowerCase() === (toHex(c) || "").toLowerCase()}
                    onClick={() => applyUpdate((x) => ({ ...x, skinColor: c }))}
                  />
                ))}
              </div>
              <ColumnPager>
                {faceOptions.map((n) => (
                  <Tile key={`face-${n}`} selected={cfg.faceType === n} onClick={() => setNumeric("faceType", n)}>
                    <Avatar config={{ ...cfg, faceType: n }} width="100%" height="100%" />
                  </Tile>
                ))}
              </ColumnPager>
            </div>
          )}

          {tab === "Hair" && (
            <div>
              <div className="swatch-row">
                {HAIR_COLORS.slice(0, 7).map((c) => (
                  <Swatch
                    key={c}
                    color={toHex(c) as string}
                    selected={(toHex(cfg.hairColor) || "").toLowerCase() === (toHex(c) || "").toLowerCase()}
                    onClick={() =>
                      applyUpdate((prev) => {
                        const selectedHex = (toHex(c) || "").toLowerCase();
                        const currentBgHex = (toHex(prev.bgColor) || "").toLowerCase();
                        const bodyHex = (toHex(prev.bodyColor) || "").toLowerCase();
                        let nextBg = prev.bgColor;
                        if (ENABLE_BG_ANTI_COLLISION && selectedHex && currentBgHex && selectedHex === currentBgHex) {
                          const candidates = BG_COLORS.filter((b) => {
                            const bh = (toHex(b) || "").toLowerCase();
                            return bh !== selectedHex && (!!bodyHex ? bh !== bodyHex : true);
                          });
                          if (candidates.length > 0) {
                            const idx = Math.floor(Math.random() * candidates.length);
                            nextBg = candidates[idx];
                          }
                        }
                        return { ...prev, hairColor: c, bgColor: nextBg };
                      }, "Set hairColor")
                    }
                  />
                ))}
              </div>
              {(() => {
                const group0 = hairOptions.filter((n) => n < 100);
                const group1 = hairOptions.filter((n) => n >= 100);
                const zipped: number[] = [];
                const m = Math.max(group0.length, group1.length);
                for (let i = 0; i < m; i++) {
                  if (i < group0.length) zipped.push(group0[i]); // first row (0xx)
                  if (i < group1.length) zipped.push(group1[i]); // second row (1xx)
                }
                return (
                  <ColumnPager>
                    {zipped.map((n) => (
                      <Tile
                        key={`hair-${n}`}
                        selected={cfg.hairType === n}
                        onClick={() => toggleNumeric("hairType", n)}
                      >
                        <Avatar config={{ ...cfg, hairType: n }} width="100%" height="100%" />
                      </Tile>
                    ))}
                    {/* None / Bald option moved to end */}
                    <Tile key="hair-0" selected={!cfg.hairType} onClick={() => setNumeric("hairType", 0)}>
                      <Avatar config={{ ...cfg, hairType: 0 }} width="100%" height="100%" />
                    </Tile>
                  </ColumnPager>
                );
              })()}
            </div>
          )}

          {tab === "Clothing" && (
            <div>
              <div className="swatch-row">
                {CLOTH_COLORS.slice(0, 7).map((c) => (
                  <Swatch
                    key={c}
                    color={toHex(c) as string}
                    selected={(toHex(cfg.bodyColor) || "").toLowerCase() === (toHex(c) || "").toLowerCase()}
                    onClick={() =>
                      applyUpdate((prev) => {
                        const selectedHex = (toHex(c) || "").toLowerCase();
                        const currentBgHex = (toHex(prev.bgColor) || "").toLowerCase();
                        const hairHex = (toHex(prev.hairColor) || "").toLowerCase();
                        let nextBg = prev.bgColor;
                        if (ENABLE_BG_ANTI_COLLISION && selectedHex && currentBgHex && selectedHex === currentBgHex) {
                          const candidates = BG_COLORS.filter((b) => {
                            const bh = (toHex(b) || "").toLowerCase();
                            return bh !== selectedHex && (!!hairHex ? bh !== hairHex : true);
                          });
                          if (candidates.length > 0) {
                            const idx = Math.floor(Math.random() * candidates.length);
                            nextBg = candidates[idx];
                          }
                        }
                        return { ...prev, bodyColor: c, bgColor: nextBg };
                      }, "Set bodyColor")
                    }
                  />
                ))}
              </div>
              <ColumnPager>
                {clothingOptions.map((n) => (
                  <Tile
                    key={`cloth-${n}`}
                    selected={cfg.clothingType === n}
                    onClick={() => setNumeric("clothingType", n)}
                  >
                    <Avatar config={{ ...cfg, clothingType: n }} width="100%" height="100%" />
                  </Tile>
                ))}
              </ColumnPager>
            </div>
          )}

          {tab === "Details" && (
            <ColumnPager>
              {[
                ...faceTextureOptions.map((n) => ({ type: "faceTexture" as const, n })),
                ...glassesOptions.map((n) => ({ type: "glasses" as const, n })),
                ...centerClothingOptions.map((n) => ({ type: "centerClothing" as const, n })),
                ...rightClothingOptions.map((n) => ({ type: "rightClothing" as const, n })),
                ...leftClothingOptions.map((n) => ({ type: "leftClothing" as const, n })),
              ].map((it) => {
                const selected =
                  (it.type === "faceTexture" && cfg.faceTexture === it.n) ||
                  (it.type === "glasses" && cfg.glasses === it.n) ||
                  (it.type === "centerClothing" && cfg.centerClothing === it.n) ||
                  (it.type === "rightClothing" && cfg.rightClothing === it.n) ||
                  (it.type === "leftClothing" && cfg.leftClothing === it.n);

                const onClick = () => {
                  if (it.type === "faceTexture") toggleNumeric("faceTexture", it.n);
                  if (it.type === "glasses") toggleNumeric("glasses", it.n);
                  if (it.type === "centerClothing") toggleNumeric("centerClothing", it.n);
                  if (it.type === "rightClothing") toggleNumeric("rightClothing", it.n);
                  if (it.type === "leftClothing") toggleNumeric("leftClothing", it.n);
                };

                // For preview tiles in Details: show only the specific item, others reset to 0
                const previewCfg: AvatarConfig = {
                  ...cfg,
                  faceTexture: 0,
                  glasses: 0,
                  centerClothing: 0,
                  rightClothing: 0,
                  leftClothing: 0,
                };
                if (it.type === "faceTexture") previewCfg.faceTexture = it.n;
                if (it.type === "glasses") previewCfg.glasses = it.n;
                if (it.type === "centerClothing") previewCfg.centerClothing = it.n;
                if (it.type === "rightClothing") previewCfg.rightClothing = it.n;
                if (it.type === "leftClothing") previewCfg.leftClothing = it.n;

                return (
                  <Tile key={`${it.type}-${it.n}`} selected={selected} onClick={onClick}>
                    <Avatar config={previewCfg} width="100%" height="100%" />
                  </Tile>
                );
              })}
            </ColumnPager>
          )}

          {tab === "Background" && (
            <ColumnPager>
              {(ENABLE_BG_ANTI_COLLISION
                ? BG_COLORS.filter((b) => {
                    const bh = (toHex(b) || "").toLowerCase();
                    const bodyH = (toHex(cfg.bodyColor) || "").toLowerCase();
                    const hairH = (toHex(cfg.hairColor) || "").toLowerCase();
                    return bh !== bodyH && bh !== hairH;
                  })
                : BG_COLORS
              ).map((c) => (
                <Tile
                  key={`bg-${c}`}
                  selected={(toHex(cfg.bgColor) || "").toLowerCase() === (toHex(c) || "").toLowerCase()}
                  onClick={() => applyUpdate((x) => ({ ...x, bgColor: c }), "Set bgColor")}
                >
                  <Avatar config={{ ...cfg, bgColor: c }} width="100%" height="100%" />
                </Tile>
              ))}
            </ColumnPager>
          )}
        </div>
        <div className="save-row" style={{ display: "flex", gap: 8, alignItems: "center" }}>
          <Button onClick={onSave}>Save Changes</Button>
          <Button variant="secondary" onClick={onImportPreset}>
            Import Preset
          </Button>
        </div>

        {/* current code box removed per request */}
      </div>
    </div>
  );
}
